var currencySelectElement = document.getElementById('currency-select');
export var renderCurrencySelectOptionsList = function (baseCurrencyAbbreviation, currenciesList) {
    if (!currencySelectElement) {
        return;
    }
    currencySelectElement.innerText = '';
    currenciesList.forEach(function (currencyAbbreviation) {
        currencySelectElement.insertAdjacentHTML('beforeend', "<option value=".concat(currencyAbbreviation, " ").concat(currencyAbbreviation === baseCurrencyAbbreviation ? 'selected disabled' : '', " data-currency-select-option>").concat(currencyAbbreviation, "</option>"));
    });
};
export var changeCurrencySelectOptionsAttributes = function (baseCurrencyAbbreviation) {
    if (!currencySelectElement) {
        return;
    }
    var selectOptions = document.querySelectorAll('[data-currency-select-option]');
    selectOptions.forEach(function (option) {
        var optionElement = option;
        if (optionElement.value === baseCurrencyAbbreviation) {
            option.setAttribute('selected', '');
            option.setAttribute('disabled', '');
        }
        else {
            option.removeAttribute('selected');
            option.removeAttribute('disabled');
        }
    });
    currencySelectElement.value = baseCurrencyAbbreviation;
};
